import React, { useEffect, useState } from 'react';
import { useForm } from '../../helpers/customHooks';
import validate from '../../helpers/validate';
import { observer, inject } from 'mobx-react';
import { findNumbers } from 'libphonenumber-js';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import { Link as RouterLink } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';

function MadeWithLove() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Built with love by the '}
      <Link color="inherit" target="_blank" href="https://www.linkedin.com/in/petro-tsapei-394857174">
        Petro Tsapei
      </Link>
    </Typography>
  );
}

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default inject('auth')(observer(function SignIn({ auth }) {
  const classes = useStyles();
  const {
    values,
    errors,
    handleChange,
    handleSubmit,
    setValues
  } = useForm(onSignIn, validate);

  const [needToRemember, setNeedToRemember] = useState(false);

  useEffect(() => {
    // initialize first time values
    setValues({
      phone: "",
      password: ""
    })
  }, [setValues]);

  function onSignIn() {
    let numberData = findNumbers(values.phone, 'UA', {
      v2: true
    })[0].number;

    auth.signIn({
      countryCode: numberData.countryCallingCode,
      phone: numberData.nationalNumber,
      password: values.password
    }, needToRemember);
  }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} noValidate onSubmit={handleSubmit}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="tel"
            label="Phone Number"
            name="tel"
            autoComplete="tel"
            type="tel"
            autoFocus
            onChange={e => handleChange(e.target.value, 'phone')}
            error={!!errors.phone}
            helperText={errors.phone}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={e => handleChange(e.target.value, 'password')}
            error={!!errors.password}
            helperText={errors.password}
          />
          <FormControlLabel
            control={
              <Checkbox
                value="remember"
                color="primary"
                onChange={e => setNeedToRemember(e.target.checked)}
              />
            }
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign In
          </Button>
          <Grid container>
            {/*<Grid item xs>*/}
            {/*  <Link component={RouterLink} to="/forgot-password" variant="body2">*/}
            {/*    Forgot password?*/}
            {/*  </Link>*/}
            {/*</Grid>*/}
            {/*<Grid item>*/}
            {/*  <Link component={RouterLink} to="/sign-up" variant="body2">*/}
            {/*    {"Don't have an account? Sign Up"}*/}
            {/*  </Link>*/}
            {/*</Grid>*/}
          </Grid>
        </form>
      </div>
      <Box mt={5}>
        <MadeWithLove />
      </Box>
    </Container>
  );
}))
